document.addEventListener( "DOMContentLoaded", () => {
    // create basic vars.
    const $amount      = document.querySelector( "#amount" );
    const $from        = document.querySelector( "#trade__from" );
    const $to          = document.querySelector( "#trade__to" );

    const $output      = document.querySelector( '#output' );

    const $list        = {
        from: document.querySelector( '#from' ),
        to:   document.querySelector( '#to' )
    };

    // events
    $amount.addEventListener( 'keydown', update );
    $from.addEventListener( 'keydown', update );
    $to.addEventListener( 'keydown', update );
    
    const url          = "https://script.google.com/macros/s/AKfycbyPlzCORaXVXcqNEvdhk35MY9ZaqPbDXiskv4EJZH0RkpJxjCI/exec";

    const fetchPromise = fetch( url );

    let data           = null;

    fetchPromise.then( response => {
        response.json().then( result => {
            updateData( result.result );
            data = result.result;
        });
    });

    fetchPromise.catch( msg => {
        //alert( "Возникла непредвиденная ошибка, попробуйте перезагрузить страницу." );
        console.error( msg );
    });


    // update data
    function updateData( data ) {

        let options = '';

        data.forEach( item => {
            options += template( item );
        })

        $list.from.innerHTML = options;
        $list.to.innerHTML   = options;
    }

    function template( item ) {
        return `
            <option value="${item[0]}">${item[1]}</option>
        `;
    }

    function update() {

        if ( data && this !== $amount ) {
            const index = [ ...data ].findIndex( item => {
                return item[0] === this.value;
            });

            this.setAttribute( 'data-index', index );
        }

        if ($amount.value < 0){
            $output.innerHTML = `Введите положительное число предметов.`;
        }
        else
        if ( $amount.value !== '' && $from.value !== '' && $to.value !== '' && data ) {
            const fromIndexPointsValue = data[ $from.getAttribute( 'data-index' ) ][1];
            const toIndexPointsValue   = data[ $to.getAttribute( 'data-index' ) ][1];
            const amountValue          = $amount.value;

            const output               = Math.floor( ( fromIndexPointsValue * amountValue ) / toIndexPointsValue );
            
            if ( fromIndexPointsValue === '' && toIndexPointsValue === '' ) {
                $output.innerHTML = `Оба предмета запрещены к продаже`;
            }
            else
            if ( fromIndexPointsValue === '') {
                $output.innerHTML = `Первый предмет запрещен к продаже`;
            }
            else
            if ( toIndexPointsValue === '') {
                $output.innerHTML = `Второй предмет запрещен к продаже`;
            }
            else
            if ( fromIndexPointsValue === '' || toIndexPointsValue === '' ) {
                $output.innerHTML = `Упс, у предмета, который вы хотите купить/продать нет цены. Выберите другой.`;
            }

            else 
            if ( output === 0 ) {
               $output.innerHTML = `У вас слишком мало товаров для обмена. Добавьте больше.`;
            }
            else 
            if ( isNaN( output ) ) {
                $output.innerHTML = 'Произошла ошибка, пожалуйста перезагрузите страницу.';
            }
            else {
                $output.innerHTML = `Количество: &nbsp; <span class="output__value">${output}</span>`;
            }
        }
    }
});
